<template>
  <div class="main dataSummary quality-info">
    <div class="main-search">
      <div class="search-item">
        <span class="item-left">开始日期</span>
        <span class="item-right">
          <el-date-picker
            v-model="search.beginCalldate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择日期"
            @change="getSbeginTime">
         </el-date-picker>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">结束日期</span>
        <span class="item-right">
            <el-date-picker
              v-model="search.endCalldate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期"
              :picker-options="endDatePicker"
              @change="getSendTime">
         </el-date-picker>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">外呼团队</span>
        <span class="item-right">
          <el-select multiple collapse-tags v-model="search.corp" @change="changeStaff('corp')">
              <template v-for="item in teamData">
                  <el-option :label="item.memo" :value="item.id"  :key="item.id">{{item.memo}}</el-option>
              </template>
           </el-select>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">品牌</span>
        <span class="item-right">
          <el-select multiple collapse-tags v-model="search.proj" @change="changeStaff('proj')">
              <template v-for="item in projData">
                  <el-option :label="item.memo" :value="item.id"  :key="item.id">{{item.memo}}</el-option>
              </template>
           </el-select>
        </span>
      </div>


      <div class="search-item">
        <span class="item-left">数据批次</span>
        <span class="item-right">
          <el-input placeholder="点击选择" readonly style="cursor: pointer;" v-model="search.modelName" @click.native="dialog_batch"></el-input>
        </span>
      </div>
      <button type="button" class="btn-search" @click="init_group_datas">查询</button>
    </div>
    <div class="main-table">
      <div class="table-msg">
        <div class="msg-left">
          <p>查询符合上述条件的数据<span>{{list.totalCount}}</span>条</p>
        </div>
      </div>
      <el-table
        :data="list.data"
        style="width: 100%"
        border
        show-summary
        :summary-method='getTotal'
        :default-sort = "{prop: 'date', order: 'descending'}"
      >
        <el-table-column
          prop="calldate"
          align="center"
          label="日期"
          width="100"
          :formatter="formatDate"
          sortable>
        </el-table-column>
        <el-table-column
          prop="crop"
          align="center"
          label="外呼团队"
          :formatter="formatCorp">
        </el-table-column>
        <el-table-column
          prop="porj"
          align="center"
          label="品牌"
          :formatter="formatProj">
        </el-table-column>
        <el-table-column
          prop="modelname"
          align="center"
          label="数据批次"
          sortable
          width="220">
          <template slot-scope="scope">
            <el-button type="text" size="small" v-html="formatModel(scope,scope.row.modelname)" @click="modal(scope.row,scope.row.calldate, scope.row.modelname, scope.row.corp, scope.row.proj)">{{scope.row.modelname}}</el-button>
          </template>
        </el-table-column>
        <el-table-column
          prop="callout"
          align="center"
          label="外呼数">
        </el-table-column>
        <el-table-column
          prop="connect"
          align="center"
          label="接通数">
        </el-table-column>
        <el-table-column
          prop="connectRate"
          align="center"
          label="接通率">
          <template slot-scope="scope">
            <p type="text" size="small" v-if="scope.row.connectRate != 0">{{scope.row.connectRate}}%</p>
            <p type="text" size="small" v-else>{{scope.row.connectRate}}</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="leads"
          align="center"
          label="成功数"
          width="90"
          sortable>
        </el-table-column>
        <el-table-column
          prop="leadsRate"
          align="center"
          label="成功率"
          width="90"
          sortable>
          <template slot-scope="scope">
            <p type="text" size="small" v-if="scope.row.leadsRate != 0">{{scope.row.leadsRate}}%</p>
            <p type="text" size="small" v-else>{{scope.row.leadsRate}}</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="hLeadsRate"
          align="center"
          label="历史平均成功率">
          <template slot-scope="scope">
            <p type="text" size="small" v-if="scope.row.hLeadsRate != 0">{{scope.row.hLeadsRate}}%</p>
            <p type="text" size="small" v-else>{{scope.row.hLeadsRate}}</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="bLeadsRate"
          align="center"
          label="拨测成功率">
          <template slot-scope="scope">
            <p type="text" size="small" v-if="scope.row.bLeadsRate != 0">{{scope.row.bLeadsRate}}%</p>
            <p type="text" size="small" v-else>{{scope.row.bLeadsRate}}</p>
          </template>
        </el-table-column>

        <el-table-column
          prop="hCallout"
          align="center"
          label="累计外呼"
        >
        </el-table-column>
      </el-table>
      <page-table show_size="true" v-bind:cur="search.page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></page-table>
    </div>
    <el-dialog class="dialog-box" title="数据批次名" :visible.sync="chart.dialog_reason" >
      <div id="bar_dv"  class="dialogEchartsClass" :style="{width: '500px', height: '400px', margin: '0 auto'}" ></div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="chart.dialog_reason=false">关闭</el-button>
      </div>
    </el-dialog>
    <el-dialog :visible.sync="popup_show">
      <popup-box :dataList='modelData' :dataId = 'dataId' ref="getDataName"></popup-box>
      <div slot="footer" class="dialog-footer">
        <el-button @click="popup_show=false">取消</el-button>
        <el-button type="primary" @click="save">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {required} from 'vuelidate/lib/validators'
import {get_dataname, changeStaff} from '@/api/index'
import {format,getCookie} from '@/config/util'
import pageTable from '@/components/pager'
import popupBox from '@/components/popup'
import ajax from '@/config/ajax'
import Qs from 'qs'
export default {
  components:{popupBox,pageTable},
  data () {
    return { // 数据集
      search: {
        beginCalldate: new Date(), // 开始时间
        endCalldate: new Date(), // 结束时间
        corp: [0], // 外呼团队
        proj: [0], // 品牌团队
        modelName: '', // 数据批次名称
        modelValue: '', //数据批次id
        orderbyCondition: 'calldate', //排序条件
        orderbyFlag: true, // 是否倒序
        size: 10, // 每页数量
        page: 1 // 页码
      },
      list: {
        data: [], // 数据集
        totalPage: 0, // 总页数
        totalCount: 0, // 总条数
        sumsList: []   //合计数据
      },
      chart: {
        chartData: [],   //饼状图数据
        dialog_reason: false, // 饼状图显示与否
      },
      total: {
        sumcallout: '',  //总外呼数
        sumconnect: '',    //总接通数
        sumconnectrate: '',  //总接通率
        sumleads: '',    //总成功数
        sumleadsrate: '',   //总成功率
      },
      popup_show: false,
      isCorpContainAll: true,     //外呼团队是否选则了全选
      isProjContainAll: true,     //品牌是否选则了全选
      endDatePicker: this.processDate(), // 结束时间不能大于开始时间
      teamData: [],  //外呼团队字典数组
      projData: [],  //品牌字典数组
      modelData: [], //数据批次
      dataId: "1",    //数据批次、省份、城市数据id分别
      columns: ''   //获取columns
    }
  },
  created () {
    this.teamData = JSON.parse(localStorage.getItem("cropid"));
    this.projData = JSON.parse(localStorage.getItem("projid"));
    this.modelData = JSON.parse(localStorage.getItem("model"));
    this.search.corp[0] = this.teamData[0].id;
    this.search.proj[0] = this.teamData[0].id;
    this.getInitialList();
  },
  methods: {
    //查询
    init_group_datas () {
      console.log(this.search.beginCalldate)
      if(this.search.beginCalldate==null){
          this.$message({
            message: '请选择开始时间',
            type: 'warning'
          });
          return
        }
        if(this.search.endCalldate == null){
          this.$message({
            message: '请选择结束时间',
            type: 'warning'
          });
          return
        }
        if(this.search.beginCalldate !=null && this.search.endCalldate !=null){
          if(this.search.beginCalldate.getTime() > this.search.endCalldate.getTime()){
            this.$message({
              message: '开始日期不能大于结束日期',
              type: 'warning'
            });
            return
          }
        }
        
      if(this.search.corp.length == 0){
        this.$message({
          message: '请选择外呼团队',
          type: 'warning'
        });
        return
      }
      if(this.search.proj.length == 0){
        this.$message({
          message: '请选择品牌',
          type: 'warning'
        });
        return
      }
      this.search.page = 1;
      this.getInitialList();
    },
    // 时间选择器开始时间
    getSbeginTime (val) {
      this.search.beginCalldate = new Date(val)
    },
    // 时间选择器结束时间
    getSendTime (val) {
      this.search.endCalldate = new Date(val)
    },
    processDate(){
      const  self = this;
      return {
        disabledDate(time) {
          if (self.search.beginCalldate) {  //如果开始时间不为空，则结束时间大于开始时间
            return new Date(self.search.beginCalldate).getTime() > time.getTime()
          } else {
            // return time.getTime() > Date.now()//开始时间不选时，结束时间最大值小于等于当天
          }
        }
      }
    },
    //品牌及外呼团队多选
    changeStaff(type){
      if(type == 'corp'){
        if(this.isCorpContainAll){
          this.isCorpContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
          this.search.corp.splice(0, 1);// 则删除第一个全部
        }else{
          this.isCorpContainAll = this.search.corp.some(value => value === 0)
          if(this.isCorpContainAll){
            this.search.corp = [0];
          }else{
            if(this.search.corp.length == this.teamData.length-1){
              this.search.corp = [0];
              this.isCorpContainAll = true;
            }
          }
        }
      }else if(type == 'proj'){
        if(this.isProjContainAll){
          this.isProjContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
          this.search.proj.splice(0, 1);// 则删除第一个全部
        }else{
          this.isProjContainAll = this.search.proj.some(value => value === 0)
          if(this.isProjContainAll){
            this.search.proj = [0];
          }else{
            if(this.search.proj.length == this.projData.length-1){
              this.search.proj = [0];
              this.isProjContainAll = true;
            }
          }
        }
      }
    },
    modal(val, calldate, modelname, corp, proj){
      this.chart.dialog_reason = true;
      this.$nextTick(function(){
        this.drawLine(calldate, modelname, corp, proj);
      })
    },
    drawLine(calldate, modelname, corp, proj){
      let bar_dv = document.getElementById("bar_dv");
      let myChart = this.$echarts.init(bar_dv);

      get_dataname(calldate, modelname, corp, proj).then((res) => {
        this.chart.chartData = res.data;
        if(res.code == '200'){
          var chartDataList = res.data;
            var option = {
				        tooltip: {
				            trigger: 'item',
				            formatter: "{a} <br/>{b}: {c} ({d}%)"
				        },
				        legend: {
				            orient: 'vertical',
				            x: 'left',
				            data: chartDataList
				        },
				        series: [
					        {
                    name:'',
                    type:'pie',
                    radius: [0 , '70%'],
                    center: ['50%', '60%'],
                    data: (function(){
                        var rs = [];
                        for(var res in chartDataList){
                          if(chartDataList[res].refusereason!='' && chartDataList[res].refusecount != ''){
                            rs.push({
                                name :chartDataList[res].refusereason,
                                value:chartDataList[res].refusecount
                            });
                          }

                        }
                        return rs;
                    })()
                  }
				        ]
			        }
			    	myChart.setOption(option);
          }
      })
    },
    
    //获取数据
    getInitialList(){
      var params = {
        beginCalldate:format(this.search.beginCalldate, "yyyyMMdd"),
        endCalldate:format(this.search.endCalldate, "yyyyMMdd"),
        corp: this.search.corp.join(","),
        proj: this.search.proj.join(","),
        modelName: this.search.modelValue,
        orderbyCondition:this.search.orderbyCondition,
        orderbyFlag:this.search.orderbyFlag,
        page:this.search.page,
        size:this.search.size
      }
      return ajax.request({
        method:'post',
        url:'summary',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        transformRequest: [function(params) {      //在请求之前对data传参进行格式转换
          params = Qs.stringify(params)
          return params
        }],
        data:params
      }).then((res)=>{
        this.list.data=res.data.list;
        this.list.totalCount=res.data.totalCount;
        this.list.totalPage=res.data.totalPage;
        this.total.sumcallout = res.data.sumcallout;
        this.total.sumconnect = res.data.sumconnect;
        this.total.sumconnectrate = res.data.sumconnectrate;
        this.total.sumleads = res.data.sumleads;
        this.total.sumleadsrate = res.data.sumleadsrate;
      }).catch((error)=>{
        console.log(error)
      })
    },
    //获取columns
    getColumns(param){
      const {columns} = param;
      this.columns = columns;
      return [];
    },
    //计算合计
    getTotal(param){
      const {columns, data} = param;
      const sums = [];
      columns.forEach((column, index) => {
        const values = data.map(item => Number(item[column.property]));
        if(column.property === 'callout'){
          sums[index] = this.total.sumcallout;
        }else if(column.property === 'connect') {
          sums[index] = this.total.sumconnect;
        }else if(column.property === 'connectRate') {
          sums[index] = this.total.sumconnectrate+"%";
        }else if(column.property === 'leads') {
          sums[index] = this.total.sumleads;
        }else if(column.property === 'leadsRate') {
          sums[index] = this.total.sumleadsrate+"%";
        }else{
          sums[index] = '--';
        }
      })
      return sums;
    },
    //格式化日期
    formatDate(row){
        var calldate = row.calldate.toString();
        return row.calldate = calldate.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3");
    },
    //处理外乎团队的数据
    formatCorp(row){
      for(var i=0;i<this.teamData.length;i++){
        if(row.corp == this.teamData[i].id){
          return row.crop = this.teamData[i].memo;
        }
      }
    },
    //处理品牌的数据
    formatProj(row){
      var projdata = this.projData;
      projdata = projdata.slice(1);
      for(var i=0;i<projdata.length;i++){
        if(row.proj == projdata[i].id){
          return row.porj = projdata[i].memo;
        }
      }
    },
    //处理数据批次的数据
    formatModel(row,value){
      for(var i=0;i<this.modelData.length;i++){
        if(value == this.modelData[i].id){
          return value = this.modelData[i].memo;
        }
      }
    },
    dialog_batch(){
      this.popup_show = true;
    },
    save() {
      this.popup_show = false;
      if(this.$refs.getDataName.complete()[0] != ''){
        this.search.modelName = this.$refs.getDataName.complete()[0].join(",");
        this.search.modelValue = this.$refs.getDataName.complete()[1].join(",");
      }else{
        this.search.modelName = '';
        this.search.modelValue = '0';
      }
    },
    //分页
    change_data(index,size){
      this.search.page=index;
      this.search.size=size;
      this.getInitialList();
    }
  }
}
</script>

<style>
  @import "../../assets/css/pages/main.css";
  .quality-info{
    padding-left: 15px;
  }
  .dataSummary .main-search .search-item .txt-common{
    width: 150px;
  }
  .dataSummary .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width:150px;
  }
  .dataSummary .main-search .search-item{
    min-width: 100px;
  }
  .dataSummary .main-search .search-item .txt-common{
    width: 150px;
  }

  .dataSummary .main-table table tbody tr td .quality,
  .dataSummary .main-table table tbody tr td .qualified,
  .dataSummary .main-table table tbody tr td .unqualified{
    display: inline-block;
    width: 80px;
    height: 24px;
    background-color: #c2c2c2;
    color: #ffffff;
    font-size: 12px;
    line-height: 24px;
    text-align: center;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
  }
  .dataSummary .main-table table tbody tr td .qualified{
    background-color: #2cdb6e;
  }
  .dataSummary .main-table table tbody tr td .unqualified{
    background-color: #de2f2f;
  }

  .dataSummary .main-table table tbody tr a.disabled{
    color: #b4b4b4;
    cursor:no-drop;
  }

  .dataSummary .edit-cross{
    width: 80%;
    text-align: center;
    margin: 0 auto;
  }

  .dataSummary .file-upload .edit-content{
    height: 300px;
  }

  .dataSummary .file-upload .sub-content{
    padding: 10px 20px;
    border:1px solid #ff6825;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    margin-bottom: 15px;
    color: #ff6825;
    font-size: 12px;
    line-height: 24px;
  }
  .dataSummary .file-upload .sub-content p{
    margin: 0;
    padding: 0;
  }
  .dataSummary .file-upload .sub-content p span{
    font-weight: 600;
    margin-right: 5px;
  }
  .el-select-dropdown__wrap ul li{
    padding-left: 20px !important;
    display: block;
  }

/*合计行的样式*/
/*滚动条的父级更改样式*/
  .el-table{
        overflow: auto;
  }
  /*滚动条以及内容还有合计的行样式更改*/
    .el-table .el-table__body-wrapper,
    .el-table .el-table__header-wrapper,
    .el-table .el-table__footer-wrapper{
            overflow:visible;
      }
    /*这个样式写完之后会产生一条横线,需要覆盖外表格的伪类after,注意要全局样式去覆盖,不要写在scoped中,外层也不能包裹父类*/
    .el-table::after{
        position: relative !important;
    }
</style>



